
* {
  margin: 0;
  padding: 0;
}
.header {
  height: 40px;
  line-height: 40px;
  color: white;
  background: black;
  text-align: center;
}
.ul {
  margin: 10px;
}
.ul li {
  position: relative;
  height: 36px;
  line-height: 36px;
  border: solid 1px #ccc;
  border-radius: 5px;
  margin-bottom: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.close {
  display: inline-block;
  width: 30px;
  height: 30px;
  background: #ddd;
  border-radius: 50%;
  margin-top: 3px;
  margin-right: -3px;
}
.close:before {
  position: absolute;
  content: "";
  width: 26px;
  height: 2px;
  display: inline-block;
  background: white;
  top: 17px;
  right: 5px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close:after {
  position: absolute;
  content: "";
  width: 26px;
  height: 2px;
  display: inline-block;
  background: white;
  top: 17px;
  right: 5px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.ul .liauto {
  border: none;
}
.switch {
  display: inline-block;
  width: 60px;
  height: 30px;
  background: #aaa;
  position: absolute;
  right: 0px;
  border-radius: 15px;
}
.switch_on {
  background: green;
}
.switch_on span {
  margin-left: 31px !important;
}
.switch span {
  display: inline-block;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background: linear-gradient(#aaa, #fff);
  margin-top: 1px;
  margin-left: 1px;
}
.btn_box {
  margin: 10px;
}
.btn_login {
  display: inline-block;
  width: 100%;
  line-height: 36px;
  height: 36px;
  background: green;
  border-radius: 5px;
  text-align: center;
  color: white;
  text-decoration: none;
  letter-spacing: 20px;
  font-size: 18px;
  box-shadow: 2px 2px 3px #b1aeae;
}
